.overlay {
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: visibility 1s, opacity 0.5s linear;
    justify-content: center;
    align-items: center;
    position: fixed;
    display: flex;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .overlay_opened {
    visibility: visible;
    opacity: 1;
  }
    
  @media (max-width: 700px) {
    .overlay {
      z-index: 14;
    }
  }
  